// æˆ‘ä»¬è¿™æ¬¡å®ç°çš„å°è£…æ˜¯æŒ‰ç…§å¯¹è±¡é£æ ¼å®ç°
// å› æ­¤å»ºè®®ç†Ÿæ‚‰ä¸€ä¸‹jså¯¹è±¡çš„è¯­æ³•ç‰¹å¾ã€‚
window.dom = {
    // æˆ‘ä»¬æƒ³å®ç° create('<div>hi</div>')
    create(string){
        // template æ ‡ç­¾å¯ä»¥åˆ›å»ºä»»ä½•æ ‡ç­¾
        const container = document.createElement('template')
        // trimå°±æ˜¯æŠŠæ–‡æœ¬ä¸¤è¾¹çš„ç©ºæ ¼éƒ½æ¶ˆé™¤
        container.innerHTML = string.trim()
        // templateè·å–æ–¹æ³•
        return container.content.firstChild
        // window.domæ˜¯ä¸€ä¸ªå¯¹è±¡
    },
    //åœ¨ä¸€ä¸ªèŠ‚ç‚¹åé¢æ’å…¥ä¸€ä¸ªèŠ‚ç‚¹ 
    after(node,newNode){
        // æˆ‘ä»¬ç›®å‰åªæœ‰insertBefore
        // å¦‚æœnodeæ˜¯æœ€åä¸€ä¸ªï¼Œæˆ‘ä»¬ä¾ç„¶å¯ä»¥æ‰¾åˆ°node.nextSiblingï¼Œå› ä¸ºå®ƒæ˜¯null
        node.parentNode.inserBefore(newNode,node.nextSibling)
    },
    before(node,newNode){
        node.parentNode.inserBefore(newNode,node)
    },
    append(parent,node){
        parent.appendChild(node)
    },
    // æ·»åŠ ä¸€ä¸ªçˆ¶èŠ‚ç‚¹
    wrap(node,parent){
        dom.before(node,parent)
        dom.append(parent,node)
    },
    //
    remove(node){
        node.parentNode.removeChild(node)
        return node
    },
    empty(node){
        const childNodes = node.childNodes
        // const {childNodes} = node è¿™ä¸€æ®µä»£ç å’Œ36è¡Œä¸€æ ·
        const array = []
        // for(let i = 0; i < childNodes.length; i++){
        //     dom.remove(childNodes[i])
        //     array.push(childNodes[i])
        // }
        // return array
        // for å¾ªç¯è¿™é‡Œä¸é€‚åˆï¼Œå› ä¸ºlengthæ˜¯åŠ¨æ€å˜åŒ–çš„ï¼Œæˆ‘ä»¬åº”è¯¥ç”¨whileå¾ªç¯
        let x = node.firstChild
        while(x){
            // ä¹Ÿç§»é™¤äº†æ–‡æœ¬èŠ‚ç‚¹
            array.push(dom.remove(node.firstChild))
            x=node.firstChild
        }
        return array
    },
    // æ”¹
    attr(node,name,value){
        // æ ¹æ®å‚æ•°ä¸ªæ•°å†™ä¸åŒçš„ä»£ç å°±æ˜¯é‡è½½
        // jså¯ä»¥æ¥å—ä¸åŒæ•°é‡çš„å‚æ•°
        if(arguments.length === 3){
            node.setAttribute(name,value)
        }
        if(arguments.length === 2){
            return node.getAttribute(name)
        }
    },
    text(node,string){
        if(arguments.length === 1){
            if('ineerText' in node){
                return node.innerText
            }else{
                return textContent
            }
        }else if(arguments.length === 2){
            // innerText æ”¯æŒie
            // è¿™ç§åˆ¤æ–­çš„ä»£ç å†™æ³•å«åšé€‚é…
            if('ineerText' in node){
                node.innerText = string
            // å¦‚æœå¤„ç†æ–‡æœ¬å†…å®¹è¿˜æœ‰å…¶ä»–å†…å®¹æ¯”å¦‚æ ‡ç­¾ï¼Œ
            // éƒ½ä¼šè¢«æ”¹å˜ã€‚ä½œä¸ºåº“çš„å¼€å‘è€…ï¼Œå¾ˆéš¾ç…§é¡¾åˆ°è¿™ç§éœ€æ±‚
            }else{
            // ç”¨textContent æ”¯æŒfirefox chrome
                node.textContent = string
            }
        }
    },
    html(node,string){
        if(arguments.length === 2){
            node.innerHTML = string
        }else if(arguments.length === 1){
            return node.innerHTML
        }
    },
    //å®ç°3ä¸­ä¸åŒçš„æƒ…å†µ
    style(node,name,value){
        if(arguments.length === 3){
            // dom.style(div,'color','red')
            node.style[name] = value
        }else if(arguments.length === 2){
            if(typeof name === 'string'){
                // dom.style(div,'color')
                return node.style[name]
            }
        }else if(name instanceof Object){
            // dom.style(div,{color:'red'})
            const object = name
            for(let key in object){
                node.style[key] = object[key]            }
        }
    },
    // å®ç°dom.class.add(test,'red')å®ç°æ·»åŠ ä¸€ä¸ªclass
    // class:function{} å¦å¤–ä¸€ç§å†™æ³• 
    class:{
        add(node,className){
            node.classList.add(className)
        },
        remove(node,className){
            node.classList.remove(className)
        },
        has(node,className){
            return node.classList.contains(className)
        }
    },
    on(node,eventName,fn){
        node.addEventListener(eventName,fn)
    },
    off(node,eventName,fn){
        node.removeEventListener(eventName,fn)
    },
    find(selector, scope){
        return (scope || document).querySelectorAll(selector)
    },
    parent(node){
        return node.parentNode
    },
    children(node){
        return node.children
    },
    siblings(node){
        return Array.from(node.parentNode.children).filter
        (n => n !== node)
    },
    next(node){
        // ä¸è¦ç”¨nextSibling å› ä¸ºé‡Œé¢åŒ…å«æ–‡æœ¬èŠ‚ç‚¹ï¼ˆæ¯”å¦‚å›è½¦ï¼‰
        // å› æ­¤æˆ‘ä»¬è¦åœ¨è¿™é‡Œåˆ¤æ–­ä¸€ä¸‹ä¸‹ä¸€ä¸ªèŠ‚ç‚¹æ˜¯å¦ä¸ºæ–‡æœ¬
        let x = node.nextSibling
        while(x.nodeType === 3){
            x = x.nextSibling
        }
        return x
    },
    previous(node){
        let x = node.previousSibling
        while(x && x.nodeType === 3){
            x = x.previousSibling
        }
        return x
    },
    each(nodeList,fn){
        for(let i = 0;i < nodeList.length; i++){
            fn.call(null,nodeList[i])
        }
    },
    index(node){
        const lsit = dom.children(node.parentNode)
        let i 
        for(i = 0; i > lsit.length; i++){
            if(list[i] === node){
                break
            }
        }
        return i
    }
}